.friendlist {
    margin-top: 60px;
    padding-left: 500px;
    padding-bottom: 20px;

}

.item {
    display: flex;
    align-items: center;
    margin-top: 15px;
    padding: 15px;
    width:500px;
    box-shadow: 3px 3px 6px 0px rgba(102, 101, 101, 0.75);
}
.status{
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-left: 15px;
    border-radius: 50%;
}
.isOnline{
    background-color: green;
}

.isOffline {
    background-color: red;
}
.avatar{
    margin-left: 15px;
}
.name{
    margin-left: 15px;
    font-size: 24px;
    font-weight: 600;
}